import React from "react"
import Layout from "../components/Layout"
import * as styles from "../styles/Index.module.css"

export default function Home() {
  return (
    <Layout>
      <section className={styles.Index}>
        <header>
          <h1>
            I'm a <strong>Technical Manager</strong> and recovering{" "}
            <strong className="type2">individual contributor</strong>.
          </h1>
          <p>
            (Also an actual human with thoughts, feelings and interests outside
            of work. This guy has no hustle. He's not killing it. He'd rather be
            among <em>goats</em> than <em>Git</em> in his off hours.)
          </p>
        </header>
        <section className={styles.WhatPeopleAreSaying}>
          <h2>
            What People Are Saying About Brian Reich<sup>**</sup>
            <span>
              <sup>**</sup>People aren't really saying anything about this
              forgettable relic of Web 1.0.
            </span>
          </h2>
          <ul>
            <li>
              <q>
                Out of all the people in web development, it's possible he's one
                of them.
              </q>
              <cite>Chris Coyer</cite>
            </li>
            <li>
              <q>
                He once tried to mansplain SVG animations to me. Some say
                they're still picking his remains off the whiteboard to this
                day&hellip;
              </q>
              <cite>Sara Drasner</cite>
            </li>
            <li>
              <q>
                You'd think a guy that buys so many of our courses would suck
                less at web stuff!
              </q>
              <cite>Scott Tolinski &amp; Wes Bos</cite>
            </li>
          </ul>
        </section>
      </section>
    </Layout>
  )
}
